.gallery { *zoom: 1; position: relative; }
.gallery:after { content: " "; display: table; clear: both; }
.gallery__item { position: relative; margin: 0 28px 30px 0; float: left; transition: opacity 0.25s; overflow: hidden; }
.gallery__item:last-child { margin-right: 0; }
.gallery__item img { position: absolute; top: 50%; max-width: 100%; transform: translateY(-50%); }
.gallery__item:hover, .gallery__item:focus { opacity: .7; }

.gallery_video .gallery__item a { display: block; position: relative; width: 100%; height: 100%; text-decoration: none; }
.gallery_video .gallery__item a:before { display: block; position: absolute; top: 50%; left: 50%; width: 62px; height: 62px; transform: translate(-50%, -50%); border-radius: 50%; background: #fff; color: #fb2539; font: 47px/62px 'autozvuk-icon'; text-align: center; content: ""; z-index: 2; }

.tab__container { *zoom: 1; }
.tab__container:after { content: " "; display: table; clear: both; }
.tab__col-1 { width: 925px; float: left; }
.tab__col-2 { width: 325px; margin-left: 40px; padding-top: 70px; float: left; }
.tab__content { padding: 30px 0 20px; font-size: 13px; }
.tab__nav { *zoom: 1; }
.tab__nav:after { content: " "; display: table; clear: both; }
.tab__pane { display: none; padding: 0; }
.tab__pane.active { display: block; }
.tab__pane h3 { margin: 0 0  15px; font-size: 20px; }

.media { *zoom: 1; margin-bottom: -1px; padding: 24px 30px 26px; border: 1px solid #ffdc2c; }
.media:after { content: " "; display: table; clear: both; }
.media__advantages { margin: 0 0 14px; }
.media__advantages dt { display: inline-block; margin-right: 10px; color: #57ae85; font-weight: 700; }
.media__advantages dd { display: inline-block; margin: 0; }
.media__author { margin: 0 24px 0 0; float: left; font-size: 16px; font-weight: 700; }
.media__body { width: 717px; padding-right: 25px; float: left; border-right: 1px solid #e9eaeb; }
.media__content { margin-bottom: 13px; line-height: 1.56; }
.media__content p { margin-bottom: 5px; }
.media__disadvantages { margin: 0 0 13px; }
.media__disadvantages dt { display: inline-block; margin-right: 10px; color: #f8727e; font-weight: 700; }
.media__disadvantages dd { display: inline-block; margin: 0; }
.media__estimated { float: right; }
.media__foot { *zoom: 1; }
.media__foot:after { content: " "; display: table; clear: both; }
.media__head { *zoom: 1; margin-bottom: 10px; }
.media__head:after { content: " "; display: table; clear: both; }
.media__mark { width: 145px; padding-top: 6px; padding-left: 24px; float: right; }
.media__mark h5 { margin: 0 0 .5em; font-size: 16px; line-height: 1; }
.media__time { position: relative; top: 2px; float: left; color: #999; font-size: 13px; }
.media__toolbar { float: left; }
.media__toolbar .link { margin-right: 32px; }

.breadcrumbs { padding: 15px 0; font-size: 13px; }
.breadcrumbs__list { margin: 0; padding: 0; list-style: none; }
.breadcrumbs__list-item { display: inline-block; position: relative; margin-left: 40px; color: #999; }
.breadcrumbs__list-item:after { display: block; position: absolute; top: 0; left: -20px; width: 10px; height: 13px; font: 13px 'autozvuk-icon'; content: ""; }
.breadcrumbs__list-item:first-child { margin-left: 0; }
.breadcrumbs__list-item:first-child:after { display: none; }
.breadcrumbs__list-link { color: #999; text-decoration: none; }
.breadcrumbs__list-link:hover, .breadcrumbs__list-link:focus { text-decoration: underline; }
.breadcrumbs__list-link:active { color: #666; }

.card-page__buy-also { margin-bottom: 64px; }
.card-page__btns { margin-bottom: 25px; }
.card-page__choose-also { margin-bottom: 58px; }
.card-page__desc { margin-bottom: 40px; color: #777; font-size: 12px; line-height: 1.2; }
.card-page__desc p { margin-bottom: 10px; }
.card-page__desc p:last-child { margin-bottom: 0; }
.card-page__goods-status, .card-page__goods-rating, .card-page__goods-comment-btn, .card-page__goods-question-btn { display: inline-block; margin-right: 20px; margin-left: 0; line-height: 1; vertical-align: middle; }
.card-page__goods-rating { color: #ffdc2c; }
.card-page__goods-status { font-weight: 700; }
.card-page__main-desc { margin-bottom: 71px; }
.card-page__minor-info { margin-bottom: 35px; font-size: 13px; }
.card-page__price { margin-bottom: 15px; font-weight: 700; }
.card-page__similar__goods { margin-bottom: 42px; }
.card-page__tab { margin-bottom: 70px; }
.card-page .goods-code { margin-top: 5px; }
.card-page_not-available .card-page__price .price { color: #d8d9dc; }

.goods-gallery__items { width: 150px; float: left; }
.goods-gallery__preview { margin-left: 200px; text-align: center; }
.goods-gallery__preview a { display: block; }
.goods-gallery__preview img { max-width: 100%; height: auto; margin: 0 auto; }

.goods { position: relative; outline: none; }
.goods__actions { margin-bottom: 18px; }
.goods__actions .btn, .goods__actions .link-icon { margin: 0 15px 0 0; vertical-align: middle; }
.goods__actions .btn:last-child, .goods__actions .link-icon:last-child { margin-right: 0; }
.goods__actions .link-icon { display: inline-block; }
.goods__comment-btn { margin-right: 0; margin-left: 10px; float: left; font-size: 13px; }
.goods__code { color: #999; font-size: 12px; line-height: 15px; }
.goods__discount { position: absolute; top: 10px; left: 15px; padding: 8px 12px; border-radius: 3px; background: #ff8941; color: #fff; font-size: 14px; font-weight: 700; line-height: 1; }
.goods__info { *zoom: 1; margin-bottom: 10px; line-height: 15px; }
.goods__info:after { content: " "; display: table; clear: both; }
.goods__name { margin-bottom: 6px; font-size: 15px; line-height: 1.3; }
.goods__name a:visited { color: #9d5faf; }
.goods__name a:visited:hover, .goods__name a:visited:focus { color: #fb2539; text-decoration: underline; }
.goods__name a:visited:active { color: #777; }
.goods__photo { margin-bottom: 7px; text-align: center; }
.goods__photo a { display: block; }
.goods__photo img { display: inline-block; }
.goods__photo_234 { height: 234px; }
.goods__photo_195 { height: 195px; }
.goods__photo_182 { height: 182px; }
.goods__price { margin-bottom: 9px; }
.goods__price .price { font-size: 30px; font-weight: 700; }
.goods__price .price__currency { margin-left: .5em; font-size: .5em; }
.goods__price .price_old { font-size: 14px; }
.goods__rating { *zoom: 1; }
.goods__rating:after { content: " "; display: table; clear: both; }
.goods__rating .rating { margin-top: 3px; float: left; }
.goods [class^="goods__"]:last-child { margin-bottom: 0; }
.goods_status_not-available .goods__name a { color: #999; }
.goods_status_not-available .goods__price .price { color: #999; }
.goods_size_l .goods__info { margin-bottom: 5px; }
.goods_size_l .goods__name { margin-bottom: 8px; font-size: 13px; }
.goods_size_l .goods__price .price { font-size: 20px; }

.u-color-success { color: #57ae85; }

.u-color-error { color: #f8727e; }

.u-left { float: left; }

.u-right { float: right; }

.u-text { font-size: 13px; line-height: 1.6; }
.u-text p { margin-bottom: 25px; }
.u-text p:last-child { margin-bottom: 15px; }

.u-mb-30 { margin-bottom: 30px; }

.u-size-210by300 { width: 210px; height: 300px; }

.u-size-450by290 { width: 448px; height: 290px; }


